
@use './variables' as *;
/* General Styles */
body {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        font-family: sans-serif;
        background-color:$backgroundColorHeroArea;
        height: 125vh;
}

/* Navbar styles */
.app-nav{
        display: flex;
        padding: .5rem;
        justify-content:space-between;
        align-items: center;
}

.navImage{
        width: 120px;
}

.nav-list{
        color: rgb(107, 106, 106);
        display: flex;
        justify-content: space-evenly;
        width: 40%;
        list-style: none;
        display: flex;
        align-items: center;
}

ul > li:nth-child(5){
        font-size: 1rem;
        color: #fff;
        background-color:#FF595E;
        border: 1px solid #FF595E;
        padding: 10px 10px;
        border-radius: 7px;
        width: 4rem;
        display: flex;
        justify-content: center;
        align-items: center;
}


/* main hero section */
.app-hero{
        width: 80%;
        height: 80vh;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
}

.app-hero-title{
        color: #45C1FF;
        letter-spacing: 0.2rem;
        font-weight: 600;
        font-size: 0.8rem;
}

.app-hero-subTitle{
        font-size: 4rem;
        text-align: center;
        color: #0A2463;
        font-weight: 700;
}

.app-paragraph{
        width: 50%;
        text-align: center;
        line-height: 1.7rem;
        font-size: 1.2rem;
        font-weight: 00;
        color: #828791;
        letter-spacing: 0.2rem;
}

.button-group{
        display: flex;
        gap: 2rem;
}

.signup{
        font-size: 1rem;
        color: #fff;
        background-color:#FF595E;
        border: 1px solid #FF595E;
        padding: 20px 20px;
        border-radius: 7px;
        width: 8rem;
        display: flex;
        justify-content: center;
        align-items: center;
}

.learn{
        font-size: 1rem;
        color: #FF595E;
        padding: 20px 20px;
        width: 8rem;
        display: flex;
        justify-content: center;
        align-items: center; 
        border: none;   
}


/* image styles */
.app-hero-container{
        display: flex;
        justify-content: center;
        background-color: #F2F5FA;
        height: 31vh;
}
.app-hero-img{
        height: 25rem;
}


.blank{
        background-color: #ffffff;
        height: 30vh;  
        margin-bottom: 0;
}

/* MainContent area styles */
.content{
        margin-top: 0;
        background-color: #ffffff;
        height: 50vh;
        display: flex;
        padding: 5rem 10rem;
}
.content-paragraph{
        margin-top: 0;
        color: #45C1FF;
        letter-spacing: 0.2rem;
        font-weight: 600;
}

.content-heading{
        font-size: 2rem;
        text-align: left;
        color: #0A2463;
        font-weight: 700;
}

.content-heading-para{
        width: 80%;
        text-align: left;
        line-height: 1.7rem;
        font-size: 1.2rem;
        font-weight: 00;
        color: #828791;
        letter-spacing: 0.2rem;
}

.content-button-group{
        display: flex;
        gap: 1rem;

}

.content-signup{
        font-size: 1rem;
        color: #fff;
        background-color:#2d3b8c;
        border: 1px solid #2d3b8c;
        padding: 20px 20px;
        border-radius: 7px;
        width: 8rem;
        display: flex;
        justify-content: center;
        align-items: center;
}

.content-learnMore{
        font-size: 1rem;
        color: #2d3b8c;
        padding: 20px 20px;
        width: 8rem;
        display: flex;
        justify-content: center;
        align-items: center; 
        border: none; 
        background-color: #ffffff;
}
.content-list ul {
        list-style-type: none;
        padding: 1rem 2rem;
        background-color: #dadfe971;
        border-radius:5px ;
    }
    
    .flex-item {
        display: flex;
        align-items: center;
        gap: 15px; 
        margin-bottom: 20px; 
        /* padding: 1.1rem; */
    }
    
    .circle {
        width: 100px;
        height: 50px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1.2rem;
        color: white;
        font-weight: bold;
        background-color: #3498db; 
    }
    
    .circle1 {
        background-color: #2f52e0; 
    }
    
    .circle2 {
        background-color: #f9cb40; 
    }
    
    .circle3 {
        background-color:#ff595e; 
    }


    .circle-para{
        color: #8c8b8b;
    }
    